/* Variables
/* ---------------------------------------------------------- */

:root {

    /* Colours */
    --color-primary: var(--ghost-accent-color);
    --color-background: #fff;
    --color-highlight: #fdffb6;
    --color-selection: #cbeafb;
    --color-selection-text: #000;
    --color-text: #273033;
    --color-text-alt: #848e95;
    --color-grad-top: #d7d7d7;
    --color-code-clock-bg: #1a1a1a;
    --color-code-clock-text: #fff;
    --color-inline-code-border: #e1eaef;
    --color-inline-code-text: #dc0050;
    --color-inline-code-bg: #f0f6f9;
    --color-bookmark-bg: #fff;
    --color-border: #e2e8ec;

    /* Fonts */
    --font-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    --font-serif: Lora, serif;
    --font-mono: Menlo, Courier, monospace, monospace;

    /* Sizes */
    --height: 4rem;
    --margin: 2rem;
    --radius: 0.5rem;
}

@media (max-width: 899px) {
    :root {
        --content-margin: calc(2vmin + 1em);
    }
}

@media (min-width: 900px) {
    :root {
        --content-margin: calc(4vmin + 1em);
    }
}

